// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import axios from "axios";
import App from "./App";
import router from "./router";
import store from "./control/index";
import VueClipboard from "vue-clipboard2";
import publicMethod from "./publicMethod/public.js";
import $ from "jquery";
import "./config/rem";
// import '../static/css/mui.css';
import "./style/base.css";
import "mint-ui/lib/style.css";
import Bus from "./eventbus";
import MintUI from "mint-ui";
import VueLazyLoad from "vue-lazyload";
import ElementUI from "element-ui";
import "element-ui/lib/theme-default/index.css";
import "../static/js/mescroll.m.js";
Vue.use(ElementUI);
Vue.use(VueLazyLoad, {
  preLoad: 1.3,
  error: "./static/ggt@2x.png",
  loading: "./static/ggt@2x.png"
});
//添加过滤器
Vue.filter("keepTwoNum", function(value) {
  value = Number(value);
  var b = value.toFixed(3);
  return b.substring(0, b.toString().length - 1);
});
Vue.filter("formatDate", function(value) {
  var time = new Date(Number(value) * 1000);
  var Y = time.getFullYear();
  var m = time.getMonth() + 1;
  var d = time.getDate();
  var H = time.getHours();
  var mi = time.getMinutes();
  var s = time.getSeconds();
  if (m < 10) {
    m = "0" + m;
  }
  if (d < 10) {
    d = "0" + d;
  }
  if (H < 10) {
    H = "0" + H;
  }
  if (mi < 10) {
    mi = "0" + mi;
  }
  if (s < 10) {
    s = "0" + s;
  }
  return Y + "." + m + "." + d;
});
// import { Spinner } from 'mint-ui'
import "../config/globle.js"; //常量
const user_id = sessionStorage.getItem("user_ID") || "",
  //URl = 'http://www.shopsn_busi.com',
  URl = "http://www.5288china.com",
  client_type = 1, //1浏览器，2 app
  andr_version = "2.2.1", //安卓APP版本0801
  ios_version = "1.0.0", //ios APP版本
  load_wrap = true,
  down = true; //true时默认打开下拉刷新

Vue.config.productionTip = false;
Vue.prototype.publicMethod = publicMethod;
Vue.prototype.axios = axios;
Vue.prototype.URL = URl;
Vue.prototype.load_wrap = load_wrap;
Vue.prototype.user_id = user_id;
Vue.prototype.client_type = client_type;
Vue.prototype.ios_version = ios_version;
Vue.prototype.andr_version = andr_version;
Vue.prototype.down = down;
Vue.prototype.$bus = Bus;

Vue.use(MintUI);
Vue.use(VueClipboard);
import { Indicator } from "mint-ui";
Vue.directive("title", {
  inserted: function(el, binding) {
    document.title = el.innerText;
    el.remove();
  }
});

router.beforeEach((to, from, next) => {
  // Indicator.open('加载中...');
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  switch (to.path) {
    case "/home":
      sessionStorage.setItem("router_index", 0);
      break;
    case "/marketHome":
      sessionStorage.setItem("router_index", 1);
      break;
    case "/groupList":
      sessionStorage.setItem("router_index", 2);
      break;
    case "/cart":
      sessionStorage.setItem("router_index", 3);
      break;
    case "/person":
      sessionStorage.setItem("router_index", 4);
      break;
  }
  next();
});
router.afterEach(router => {
  setTimeout(function() {
    Indicator.close();
  }, 100);
});

new Vue({
  el: "#app",
  router,
  store,
  template: "<App/>",
  components: { App }
});



