import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const state = {
    //创建数据仓库
    /*
        数据
    */
    select_coupon:'',//优惠券
    groupScrollY: 0,
    groupData: [],
    couponStatus: '',
    listData: [],
    listInfo: {},
    changeHomeScrollY: 0,
    rollingNode: 0,
    recruitScrollY: 0,
    link_id: '',
    sliding_data_sw: false, //滑动时没数据的开关
    no_data_sw: false, //第一次没数据时的开关
    load_show: true, //加载状态
    roll_switch: true, //滚动加载数据开关
    slide_switch: false, //避免多次请求开关
    sort_page: 1, //分类分页
    search_value: '', //搜索内容
    sort_status: null, //搜索分类状态
    sort_id: null, //搜索分类id
    search_data: [], //搜索数据
    goods_id: '', //选取规格后的商品id
    show_download: false,
    buy_text_status: '',
    no_data: false,
    page_text: '加载中...',
    loading_status: false,
    page: 1, //订单分页
    goods: '', //结算商品信息
    fit: true, //管理收货地址 开关
    price: '', //结算价格
    order_number: '', //订单号
    footprint: '', //我的足迹
    order: '', //我的订单
    home_data: '', //主页
    braDetails: '', //品拍馆详细
    news_data: '', //我的消息
    news_con: '', //我的消息-内容
    my_wallet: '', //我的钱包
    user_Imag: '', //用户头像
    commodity_val: 1, //商品购买选择个数 默认为1
    commodity_number: '', //商品购买选择个数
    commodity_data: '', //商品详情
    commodity_data1: '',
    catr_number: true, //购物车数量
    transformation_num: null, //转换后的购物车数量
    cart_data: '', //购物车
    cart_computer: '', //感兴趣的物品
    dataLeave: '', //猜你喜欢
    nowTime: '', // 倒计时

    /*
        状态
    */
    const_join: false, //商品详情按钮状态
    load_wrap: true, //home页面加载动画开关
    cart_load: true, //购物车load
    class_load: true, //分类load
    order_load_wrap: true, //订单load动画
    invoice: false, //发票信息 默认为不开发票
    type: 0, //发票状态
    rise: 0, //发票状态
    content: 0, //发票状态
    type_data: [],
    res_data: ['个人', '单位'], //发票抬头
    content_data: ['办公用品', '耗材', '电脑配件', '明细'], //发票内容
    invoice_switch: [{ invoice_title: '普通发票', invoice_type: '个人', content: '办公用品' }], //发票信息 设置默认为个人

    /*
        订单
    */
    order_title: '', //订单头部
    order_load: false, //订单ajax切换状态
    order_status: '', //订单状态 空为全部订单，-1：已取消；0 待付款，1，待处理，3待收货，4已完成
    status: '', //头部展示状态
    order_ommodity: '', //评论商品-商品信息储存
    order_details: '', //订单详情
    logis_data: '', //物流信息
    repair: '', //退货返修

    /*
        退款
    */
    setvuce_data: '', //申请退货数据
    /*
        我的评论
    */
    my_comment: '', //我的评论
    chart_review: '' //有图评论
};
const mutations = {
    changeRecruitScrollY(state, recruitScrollY) {
        state.recruitScrollY = recruitScrollY;
    },
    logis(state, inf) {
        //变更物流信息
        state.logis_data = inf;
    },
    setvuce(state, inf) {
        //变更退货数据
        state.setvuce_data = inf;
    },
    my_comment(state, inf) {
        //我的评论
        state.my_comment = inf;
    },
    chart_review(state, inf) {
        //有图评论
        state.chart_review = inf;
    },
    value(state, inf) {
        //默认商品购买数量
        state.commodity_val = 1;
    },
    shops_switch(state, inf) {
        //商品详情按钮开关
        state.const_join = false;
    },
    page_text(state, inf) {
        //默认加载更多
        state.page_text = '加载中...';
    },
    clear_page(state, inf) {
        //默认加载更多
        state.page = 1;
    },
    clear_data(state, inf) {
        state.commodity_data = '';
    },
    buy_text(state, inf) {
        state.buy_text_status = inf;
    }
};
const store = new Vuex.Store({
    state,
    mutations
});
export default store;


